    font-weight: 500; 
    text-align: center; 
 }

 .text__container {
    margin: 50px;
 }

.devider{
    margin-top: 55px;
    color: grey;
}

.login {
    background-color: white;
    text-align: center;
    -webkit-transition: all ease-in-out 0.5s;
    transition: all ease-in-out 0.5s;
}

.login__title {
    font-family: 'Megrim';
    font-size: calc(10vh - 20px);
}

.login__field {
    display: inline-block;
    margin: 0 auto;
    margin-top: 20px;
    margin-right: 2px;
    border: none;
    font-size: calc(8px + 1vw);
    height: 50px;
    width: 90%;
}

.login__field:focus {
    border-color: black;   
}

.login__field--signUp::after {
    content: 'XXXXXXXX';
}

.login__button {
    margin-top: 35px;
    height: 50px;
    font-size: 18px;
    color: white;
    border: transparent;
    border-radius: 25px;
    width: 30vw
}

.login__button--email {
    background-color: #e83c51;
}

.login__button--email:active {

    background-color:#bf0f24;
}

.login__button--cancel {
    background-color: #404040;
}

.login__button--cancel:active {

    background-color:#1f1f1f;
}

.link {
    text-decoration: underline;
    color: #3ba2f7;
    cursor: pointer;
}

.container__textField {
    margin: auto;
    width: calc(120px + 30vw);
    border-bottom: 2px solid darkgrey;
}

.container__signUp{
    display: block;
    margin: 0 auto;
}

.login__icon {
    color: darkgray;
    position: relative;
    padding-left: 2px;
    background-color: white;
    cursor: pointer;
    -webkit-transition: all ease 0.2s;
    transition: all ease 0.2s;
}

.login__icon:hover {
    color: lightskyblue;
}
.container__floatingField {
  margin-top: 22px;
  margin-bottom: 22px;
}

.floatingField_container {
  margin-top: 2%;
  padding-top:10px;
  padding-bottom:10px;
  border-radius: 5px;
  border: 1px solid darkgray;
  display:block;
}

.floatingField_container:focus-within {
  border-color: #0079bf;
  box-shadow: 0px 0px 0px 4px rgba(61, 150, 252, 0.3);
  -webkit-transform: scale(1.005);
          transform: scale(1.005);
  transition: all ease-in-out 0.1s;
  -webkit-transition: all ease-in-out 0.1s;
}

.floatingField_container--error {
  margin-top: 2%;
  padding-top:10px;
  padding-bottom:10px;
  border-radius: 5px;
  border: 1px solid darkgray;
  display:block;
}

.floatingField_container--error:focus-within {
  border-color: #e74a44;
  box-shadow: 0px 0px 0px 4px rgba(255, 30, 22, 0.3);
  -webkit-transform: scale(1.005);
          transform: scale(1.005);
  transition: all ease-in-out 0.1s;
  -webkit-transition: all ease-in-out 0.1s;
}

.floatingField__icon {
  margin-left: 12px;
  padding-right: 4px;
  font-size: 18px;
  color: rgba(29,28,29,0.7);
  line-height: 1.33333333;
}

.floatingField__input {
  width: 85%;
  font-size: calc(2vw - 15px);
  border: none;

}

.floatingField__length {
  padding-right: 8px;
  font-weight: 600;
}

.floatingField__length--ok {
  color:grey;
}

.floatingField__length--error {
  color: #e74a44;
}

.floatingField__header {
  color: #1d1c1d;
  position: relative;
  display:inline-block;
  padding-right: 4px;
  font-weight: 600;
  line-height: 8px;
  text-align: left;
}
.floatingField__optional {
  position: relative;
  display: inline;
  text-align: left;
  font-size: 200;
  color: #545454;
}

.floatingField_container--header {
  display: block;
  text-align: left;
}

.floatingField__desc {
  font-weight: bold;
  color: #e8912d;
}

.popUp__top{
  text-align: left;
  padding: 20px 28px;
  color: #1d1c1d;
  align-items: center;
  display: flex;
  width: 100%;
}

.popUp__header {
  font-size: 28px;
  font-weight: 800;
}

.closeButton__container {
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 1;
}

.popUp__closeButton {
  position: relative;
  display: inline-flex;
  height: 36px;
  width: 36px;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}
.popUp__closeButton::before{
  content:"\00d7";
  font-size: 26px;
  font-style: normal;
  font-weight: 300;
  line-height: 1;
}

.popUp__closeButton:hover {
  background-color: rgba(29,28,29, 0.13);
  color:black;
}

.popUp__closeButton:active {
  background-color: rgba(0,0,0,0.3);
}

.popUp_briefDesc {
  font-size: 15px;;
  color: #6d6c6c;
}

.popUp__body{
  padding: 0px 26px;
}

.popUp__bottom {
  padding: 24px 26px;
  width: calc(100% - 52px);
  position: relative;
  display: inline-flex;
  justify-content: initial;
  text-align: right;
}

.basicButton {
  text-align: center;
  font-size: 15px;
  height: 36px;
  min-width: 80px;
  padding: 0 12px 1px;
  font-weight: bold;
  border-radius: 4px;
}
.basicButton--disabled {
  background-color: rgba(29,28,29,0.13);
}

.basicButton--disabled {
  background-color: darkgrey;
}

.basicButton--enabled {
  background-color:green;
}

.toggle__container {
  width: 56px;
  height: 30px;
  border-style:solid;
  border-width: 1px;
  border-color: #4f4f4f;
  border-radius: 16px;
  display: flex;
  position: relative;
  align-items: center; 
  cursor: pointer;
  float:right;
}
.toggle__container:hover {
  box-shadow: inset 0px 0px 0px 1px rgba(79, 79, 79,1);
}

.toggle__container:focus {
  box-shadow: 0px 0px 0px 4px rgba(61, 150, 252, 0.3);
}

.toggle__container--selected {
  width: 54px;
  height: 30px;
  border-style:solid;
  border-width: 1px;
  border-color: #1b825c;
  border-radius: 16px;
  background-color: #1f8e65;
  display: flex;
  position: relative;
  align-items: center; 
  cursor: pointer;
  float:right;
}

.toggle__childSwitch {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  left:6px;
  background-color: #5e5e5e;
  transition: all ease-in-out 0.2s;
  -webkit-transition: all ease-in-out 0.2s;
}

.toggle__childSwitch--selected {
  background-color: white;
  transform: translateX(22px);
  -webkit-transform: translateX(22px);
}

.toggle__check {
  color: white;
  padding-left: 10px;
}

.toggleDesc__container {
  width: 60%;
  display: inline-block;
}

.container__switch {
  width: 40%;
  position: relative;
  display: inline-block;
}

.container__desc {
  display: flex;
  width: 100%;
  position: relative;
  align-items: center; 
  margin-top:30px;
  margin-bottom:30px;
}

.floatingField__underField {
  padding-top: 4px;
  font-size: 13px;
  color: rgba(29, 28, 29, 0.7);
}

.toggle__text {
  font-size: 15px;
  line-height: 1.46668;
  font-weight: 400;
  color: rgba(29, 28, 29, 0.7);
}

.status-indicator::before {
  content: "\2022";
  color: #38e500;
  margin-right: 6px;
  font-size: 18px;
}

.status-indicator {
  margin-top: -4px;
  margin-bottom: -4px;
}

.status-username {
  font-size: 14px !important;
  line-height: 1.46668;
  font-weight: 400;
  color: white;
  opacity: 0.7;
}

.status-header {
  letter-spacing: -0.2px;
  font-size: 18px;
}

.main__layout--userInfo:hover .status-username {
  opacity: 1;
}

.chevron {
  display: inline-block;
  vertical-align: middle;
  padding-left: 4px;
  font-size: 11px !important;
  opacity: 0.7;
  margin-bottom: 5.5px;
}

.main__layout--userInfo:hover .chevron {
  opacity: 1;
}

.input-field-container {
  display: flex;
  position: relative;
  width: calc(100vw - 272px);
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 22px;
  border-width: 1px;
  border-radius: 4px;
  border-style: solid;
  border-color: rgba(29,28,29,.5);
  padding-bottom: 4px;
  padding-top: 4px;
  padding-right: 2px;
  padding-left: 4px;
  min-width: 1px;
}

.input-field-container--inputField {
  text-align: left;
  width: auto;
  display: flex;
  width: 100%;
  font-size: 15px;
  padding-bottom: 4px;
  padding-left: 8px;
  border: none;
  position: relative;
  line-height: 1.46668;
}

.input-field-container--inputField::-webkit-input-placeholder {
  text-align: left;
  color: rgba(29,28,29,.45);
}

.input-field-container--inputField::-moz-placeholder {
  text-align: left;
  color: rgba(29,28,29,.45);
}

.input-field-container--inputField:-ms-input-placeholder {
  text-align: left;
  color: rgba(29,28,29,.45);
}

.input-field-container--inputField::-ms-input-placeholder {
  text-align: left;
  color: rgba(29,28,29,.45);
}

.input-field-container--inputField::placeholder {
  text-align: left;
  color: rgba(29,28,29,.45);
}

.input-field-container--icon {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 2px;
  min-height: 32px;
  max-height: 32px;
  min-width: 32px;
  max-width: 32px;
  z-index: 200;
  color: rgba(29,28,29,.45);
}

.input-field-container--icon:hover {
  background-color: #007a5a;
  color: white;
}

